@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&family=Ubuntu&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,300;1,500&display=swap);

body {
  background-color: #212121;
}

.App {
  height: 100vh;
}

.navbar {
  font-family: "Open sans", sans-serif;
  margin: 0;
  padding: 0;
  display: flex;
  height: 140px;
  width: 100%;
  justify-content: space-around;
  align-items: center;
  background-color: transparent;
}
.brand h1 {
  color: goldenrod;
}
.brand {
  color: white;
  font-family: "Poppins";
  font-weight: bold;
  position: relative;
  left: 20px;
  font-size: 1.3em;
}

.nav-list {
  list-style: none;
  display: flex;
  align-items: center;
  grid-gap: 2em;
  gap: 2em;
}
.nav-list li a {
  color: white;
  text-decoration: none;
  border: 2px solid transparent;
  padding: 10px;
}
.nav-list li a:hover {
  color: goldenrod;
  transition: all 0.4s ease-in-out;
  border-bottom: 2px solid goldenrod;
}
.fa-search {
  position: absolute;
  cursor: pointer;
  top: 62px;

  color: white;
}
.searchBar {
  position: absolute;
  padding: 10px;
  background: none;
  box-shadow: none;
  outline: none;
  font-size: 15px;
  position: relative;
  opacity: 0;
  color: wheat;
  border: none;
  border-bottom: 3px solid goldenrod;
  width: 0;
  max-width: 0;
  transition: width 0.3s ease, max-width 0.3s ease;
}

.searchBar.active {
  overflow: hidden;
  opacity: 1;
  width: 130px;
  max-width: 130px;
}

@media (max-width: 1240px) {
  .nav-list {
    display: none;
  }
  .brand {
    position: relative;
    right: 50px;
  }
}

.burger-menu {
  display: none;
  cursor: pointer;
  z-index: 999;
  overflow: hidden;
  position: relative;
}

.bar {
  width: 25px;
  height: 3px;
  background-color: white;
  margin: 6px 0;
  transition: 0.4s;
}

.active .bar:nth-child(1) {
  -webkit-transform: rotate(-45deg) translate(-5px, 5px);
          transform: rotate(-45deg) translate(-5px, 5px);
}

.active .bar:nth-child(2) {
  opacity: 0;
}

.active .bar:nth-child(3) {
  -webkit-transform: rotate(45deg) translate(-5px, -5px);
          transform: rotate(45deg) translate(-5px, -5px);
}

.drawer {
  overflow-y: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #333333;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: -webkit-transform 0.4s ease;
  transition: transform 0.4s ease;
  transition: transform 0.4s ease, -webkit-transform 0.4s ease;
  z-index: 999;
}

.active.drawer {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.drawer-nav-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  height: 100%;
  padding: 50px;
}

.drawer-nav-list li {
  margin-bottom: 1em;
  padding: 20px;
  width: 100%;
  text-align: center;
}

.drawer-nav-list li a {
  font-family: "Poppins";
  color: white;
  text-decoration: none;
  font-size: 18px;
  padding: 20px;
  width: 100%;
}

@media (max-width: 1240px) {
  .nav-items.active {
    display: none;
  }

  .burger-menu {
    display: block;
  }

  .nav-list {
    display: none;
  }
}
#credit {
  position: absolute;
  color: rgb(214, 214, 214);
  bottom: 0;
  font-family: "Poppins";
}
.fa-heart {
  color: rgb(212, 4, 4);
}

.ui {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.Movies {
  position: relative;
  display: flex;
  justify-content: center;
  grid-gap: 2em;
  gap: 2em;
  width: 95%;
  flex-wrap: wrap;
  top: 10px;
  height: 100%;
  max-width: 1400px;
}

.movie-image {
  position: relative;
  height: 370px;
  cursor: pointer;
}
.movie-card {
  color: white;
  background-color: #333333;
  position: relative;
  width: 270px;
  height: 620px;
  box-shadow: 0px 1px 8px rgba(0, 0, 0, 0.4);
  border-radius: 8px;
  top: 70px;
  flex-shrink: 0;
}

.movie-image::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  border-radius: 8px;
  height: 30%;
  background: linear-gradient(
    to top,
    rgba(0, 0, 0, 0.7) 0%,
    rgba(0, 0, 0, 0) 100%
  );
}
.movie-image img {
  object-fit: cover;
  height: 100%;
  width: 100%;
  border-radius: 8px;
}
#title {
  color: #eeeeee;
  font-family: "Ubuntu", sans-serif;
  font-size: 18px;
  line-height: 1.4;
  font-weight: 700;
}
.movie-meta-info {
  font-family: "Ubuntu", sans-serif;
  display: flex;
  grid-gap: 1em;
  gap: 1em;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  color: #ccc;
  font-weight: 700;
  font-size: 0.8em;
  word-spacing: 2px;
  position: relative;
  bottom: 18px;
}
.movie-meta-info h4 {
  color: #e0e0e0;
}
.movie-info {
  position: relative;
  bottom: 32px;
  line-height: 1.5;
}
.movie-info p {
  line-height: 1.5;
  font-family: "Poppins", sans-serif;
  letter-spacing: 1px;
  font-weight: 300;
  font-size: 0.8em;
  color: #f7f7f7;
}

/* PLAY ICON */
.play-icon {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  z-index: 999;
}

.movie-image:hover .play-icon {
  opacity: 2;
}

.fa-play {
  font-size: 48px;
  color: white;
}
.read-more {
  font-size: 13px !important;
  color: #6fcdff !important;
  position: absolute !important;
  bottom: -40px !important;
  cursor: pointer !important;
  text-decoration: none;
}
.content {
  padding: 8px 18px;
}
.read-more:hover {
  transition: all 0.4s ease-in !important;
  font-size: 14px !important;
}

.showing {
  display: flex;
  position: relative;
  justify-content: center;
  height: 50px;
  top: 20px;
  grid-gap: 50rem;
  gap: 50rem;
  right: 10px;
  width: 100%;
  align-items: center;
}
@media (max-width: 1245px) {
  .showing {
    display: none;
  }
}
@media (max-width: 940px) {
}
.latest {
  font-family: "Ubuntu";
  letter-spacing: 1px;
  font-size: 30px;
  color: white;
}
.page-no {
  color: white;
  position: relative;
}

/* STYLES FOR PAGINATION */
.pagination {
  display: flex;
  justify-content: flex-end;
  width: 1200px;
  position: relative;
  top: 50px;
  height: 120px;
  align-items: center;
  right: 20px;
}
.pageNo {
  display: flex;
  width: 300px;
  flex-direction: row;
  grid-gap: 1em;
  gap: 1em;
}
.pagination button {
  background-color: goldenrod;
  color: black;
  cursor: pointer;
  border: none;
  opacity: 0.9;
  outline: none;
  width: 140px;
  padding: 15px 0px;
}
.pagination button:hover {
  transition: all 0.3s ease;
  opacity: 2;
}

#jumpTxt {
  position: absolute;
  outline: none;
  right: 370px;
  border: none;
  letter-spacing: 1.5px;
  padding: 10px 12px;
  /* border: 2px solid #6fcdff; */
  background-color: #333333;
  color: white;
}
#jmpBtn {
  cursor: pointer;
  background-color: #333333;
  outline: none;
  color: #ccc;
  border: 1px solid #050404;
  border-radius: 8px;
  position: absolute;
  right: 220px;
  padding: 10px;
  width: 130px;
}

@media (max-width: 620px) {
  .movie-card {
    min-width: 84%;
  }
}

.details-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.spin {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  z-index: 999;
}

.imp-details {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 3em;
  gap: 3em;
}
.details-card {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  max-width: 1200px;
  padding: 30px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: #faf9f6;
  margin: 0 auto;
}

.movie-poster {
  width: 40%;
  max-width: 400px;
  box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.4);
  border-radius: 10px;
}

.movie-poster img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.movie-details {
  width: 100%;
  max-width: 800px;
  color: #faf9f6;
}

.movie-content h1 {
  font-family: "Poppins";
  font-size: 37px;
}

p,
h4,
h3 {
  font-family: "Poppins";
  font-weight: 300;
}

p span,
h3 span,
h4 span {
  color: goldenrod;
}

.btn-cta {
  padding: 10px 17px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
  color: rgb(53, 52, 52);
  background-color: goldenrod;
  outline: none;
  cursor: pointer;
  border: none;
  opacity: 0.9;
  border-radius: 6px;
}

.btn-cta:hover {
  opacity: 2;
}

.back-btn {
  position: relative;
  top: 40px;
}

.back-btn i {
  cursor: pointer;
  color: goldenrod;
  opacity: 0.9;
  font-size: 45px;
}

.back-btn i:hover {
  opacity: 2;
}

@media (max-width: 768px) {
  .imp-details {
    flex-direction: column;
    align-items: center;
  }
  .details-container {
    margin-top: 240px;
  }
  .details-card {
    width: 90%;
    padding: 1em;
  }

  .movie-poster {
    width: 100%;
    max-width: 100%;
  }

  .movie-details {
    width: 100%;
    max-width: 100%;
  }
}

@media (min-width: 768px) {
  .details-container {
    justify-content: center;
  }

  .details-card {
    width: 80%;
    padding: 50px;
    text-align: left;
  }
}

@media (max-width: 946px) {
  .back-btn {
    left: 30px;
    top: -20px;
  }
}

